import { request } from '../utils/requestToJs';

const prefix = '/api/education';

export const getPortalCategories = ({
  label,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
} = {}) =>
  request({
    prefix,
    url: `/PortalCategory`,
    method: 'GET',
    params: { label, page, pageSize, orderBy, isPagingEnabled },
    data: {},
  });



